import ScrollViewWeb from './scroll-view-web';



export default {
  name: 'PressScrollView',
  components: {





    // eslint-disable-next-line no-dupe-keys
    PlatScrollView: ScrollViewWeb,

  },
  props: {
    scrollY: {
      type: Boolean,
      default: false,
    },
    enableFlex: {
      type: Boolean,
      default: false,
    },
    enhanced: {
      type: Boolean,
      default: false,
    },
    scrollWithAnimation: {
      type: Boolean,
      default: false,
    },
    enablePassive: {
      type: Boolean,
      default: false,
    },
    scrollAnchoring: {
      type: Boolean,
      default: false,
    },
    scrollOffset: {
      type: Number,
      default: () => 0,
    },
    finished: {
      type: Boolean,
      default: () => false,
    },
    hideLoading: {  // 是否隐藏loading动画
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: () => false,
    },
    scrollToElementById: { type: String, default: '' },
    hideNoMoreText: {
      type: Boolean,
      default: () => true,
    },
    finishedText: {
      type: String,
      default: () => '没有更多了',
    },
    scrollViewId: {
      type: String,
      default: '',
    },
  },
  emits: ['scrolltolower', 'loadmore', 'scroll'],
  data() {
    return {};
  },
  mounted() {

  },
  methods: {
    scrolltolower() {
      if (this.finished) {
        return;
      }
      this.$emit('scrolltolower');
      this.$emit('loadmore'); // 监听这个也可以，比较好记
    },
    scroll(...args) {
      this.$emit('scroll', ...args);
    },
  },
};
